






















import {
	mapState,
	mapMutations
} from 'vuex';
import global from '../../store/Global.js';
import uniCard from "../../components/uni-card/uni-card.vue";
import service from "../../service.js";

export default {
	components: {
		uniCard
	},
	data() {
		return {
			tabIndex: 0,
			tabBars: [{
					name: '待办箱',
					id: '1'
				},
				{
					name: '已办箱',
					id: '2'
				},
				{
					name: '办结箱',
					id: '3'
				}
			],
			tabItems: [
				[],
				[],
				[]
			],
			userInfo: {}
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'userName']),
	onLoad() {
		var self = this;
		this.userInfo = service.getUsers(function() {
		});
		global.checkLogin(self.userInfo, self.forcedLogin, function() {
			self.getData(0);
		});


	},
	methods: {
		tapTab(index) {
			this.tabIndex = index;
			this.getData(index);
		},
		changeTab(event) {
			this.tabIndex = event.detail.current;
			this.getData(this.tabIndex);
		},
		getData(type) {
			uni.showLoading({
				title: '正在加载数据'
			})
			var self = this;
			service.getWorkFlowList(self.userInfo.userid, type, function(res) {
				self.tabItems[type] = res;
				console.log(self.tabItems);
				//swiper组件的bug，要切换一下页面才能渲染到数据
				self.tabIndex=type+1;
				self.tabIndex=type;
				console.log(self.tabIndex);
			})
		},
		ToDetail(insid){
			uni.navigateTo({
				//url:"../wfengine/wftaskmain?insid="+insid,
				url:"../pick/pickRY?insid="+insid
			})
		},
		tabS(insid){
			uni.navigateTo({
				url:"../search/search"
			})
		}
	}

}
